body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
    text-align: center;
}
.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}
.App-intro {
    font-size: large;
}


  
.navbar {
    background: rgb(26,177,170);
    color: white;
}
.navbar a {
    color: white;
    margin-left: 15px;
}

.header {
    background: url(/static/media/headerImg.b633c8b1.jpg) no-repeat;
        background-size: cover;
        background-position: right;
    height: 75vh;
    color: rgb(26,177,170);
    
    padding-top: 25vh;
    
}
.header h1 {
    font-size: 80px;
    font-family: 'Caveat', cursive;
    background-color: white;
}
.header h2 {
    font-size: 60px;
    font-family: 'Caveat', cursive;
    background-color: white;
    padding-top: 1vh;
    padding-bottom: 1vh;
}
.header h3 {
    background-color: white;
    padding-top: 1vh;
    padding-bottom: 1vh;
}
.btn {
    background: rgb(26,177,170);
    height: 35px;
}
input {
    height: 35px;
}
form {
    width: 35vw;
    margin: 40px auto;
}
body {
    overflow-x: hidden;
    height: 100%;
    }
    .results {
        padding-top: 20px;
        background: rgb(26,177,170);
        height: 100%;
    }
    .result-box{
        background: #f9f5da
    }
    p {
        float: left;
        text-align: left;
    }
    .title, .authors {
        margin-left: 10px;
    }
    .buttonDiv {
        margin-right: 20px;
        float: right;
    }
    .title {
        margin-bottom: 0px;
        font-weight: bold;
    }
    .results h1 {
        color: white;
    }
    .view {
        background: rgb(26,177,170);
        color: white;
        margin-right: 10px;
    }
    .save {
        color: white;
    }
    .description{
        margin-right: 20px
    }
.savedHeader {
    background: url(/static/media/savedbooks.98731bff.jpg) no-repeat;
      background-size: cover;
      background-position: right;
    height: 75vh;
    color: rgb(26,177,170);
   
    padding-top: 25vh;
}
.savedHeader h1{
    font-size: 150px;
    font-family: 'Caveat', cursive;
    background-color: white;
}
.savedHeader h2 {
    font-size: 50px;
    font-family: 'Caveat', cursive;
    background-color: white;
}
